import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/index/Index.vue'
import Info from '../page/course/Detail.vue'
import Study from '../page/course/Video.vue'
import Search from '../page/discover/Search.vue'
import SearchType from '../page/discover/SearchType.vue'
import Profile from '../page/user/Profile.vue'
import Account from '../page/user/Account.vue'
import Opinion from '../page/user/Opinion.vue'
Vue.use(Router)
var routers = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    name: 'index',
    path: '/index',
    component: Index
  },
  {
    name: 'info',
    path: '/info',
    component: Info
  },
  {
    name: 'video',
    path: '/video',
    component: Study
  },
  {
    name: 'search',
    path: '/search',
    component: Search
  },
  {
    name: 'searchtype',
    path: '/searchtype',
    component: SearchType
  },
  {
    path:'/profile',
    component:Profile
  },{
    path:'/account',
    component:Account
  },
  {
    path:'/opinion',
    component:Opinion
  }
]

export default new Router({
  routes: routers
})



